module.exports = ":root {\n    --input-padding-x: .75rem;\n    --input-padding-y: .75rem;\n}\n\nhtml, body {\n    height: 100%;\n}\n\nbody {\n    display: flex;\n    align-items: center;\n    background-color: #f5f5f5;\n}\n\n.form-group {\n    width: 100%;\n    max-width: 420px;\n    padding: 15px;\n    margin: auto;\n}\n\n.form-label-group {\n    position: relative;\n    margin-bottom: 1rem;\n}\n\n.form-label-group > input, .form-label-group > label {\n    padding: var(--input-padding-y) var(--input-padding-x);\n}\n\n.form-label-group > label {\n    position: absolute;\n    top: 0;\n    left: 0;\n    display: block;\n    width: 100%;\n    margin-bottom: 0; /* Override default `<label>` margin */\n    line-height: 1.5;\n    color: #495057;\n    border: 1px solid transparent;\n    border-radius: .25rem;\n    transition: all .1s ease-in-out;\n}\n\n.form-label-group input::-webkit-input-placeholder {\n    color: transparent;\n}\n\n.form-label-group input:-ms-input-placeholder {\n    color: transparent;\n}\n\n.form-label-group input::-ms-input-placeholder {\n    color: transparent;\n}\n\n.form-label-group input::placeholder {\n    color: transparent;\n}\n\n.form-label-group input:not(:placeholder-shown) {\n    padding-top: calc(var(--input-padding-y) + var(--input-padding-y) * (2 / 3));\n    padding-bottom: calc(var(--input-padding-y) / 3);\n}\n\n.form-label-group input:not(:placeholder-shown) ~ label {\n    padding-top: calc(var(--input-padding-y) / 3);\n    padding-bottom: calc(var(--input-padding-y) / 3);\n    font-size: 12px;\n    color: #777;\n}\n\n.borderStyle {\n    border: 4px solid black;\n}\n\n.card-group {\n    opacity: 1;\n}\n\n.card {\n    max-width: 40rem;\n    height: 60vh;\n    margin-bottom: 10px;\n    border: none;\n}\n\n#post {\n    resize: none;\n}\n\n#form-signin {\n    margin-bottom: 10px;\n}\n\nvirtual-scroll {\n    display: block;\n    width: 100%;\n    /* height: 100%; */\n}\n\napp-post {\n    display: block;\n    width: 100%;\n}\n\n.total-padding {\n    height: 10px;\n}\n\n::-webkit-scrollbar { \n    display: none; \n}\n\n.round-img-container{\n\n    /* Makes a circle */\n    border-radius:50%; \n    border: 2px solid black;\n    width: 200px;\n    height: 200px;\n\n    /* Centers circle */\n    margin: auto;\n\n    /* Centers contents of the circle */\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    /* Cuts off overflow */\n    overflow: hidden;\n  }\n\n.round-contained-img{\n    max-height:100%;\n    min-width:100%;\n\n    /* Maintain aspect ratio */\n    flex-shrink: 0;\n  }"